<script setup lang="ts">
import { ref, computed, getCurrentInstance, onMounted } from "vue";
import type { ComponentInternalInstance } from "vue";
import { storeToRefs } from "pinia";
import { useI18n } from "vue-i18n";
import type { Form } from "vee-validate";
import { useCounterStore } from "@/stores/counter";
import { useMemberService } from "@/stores/memberFunction/memberService";
import { useTimeListLogic } from "@/stores/timeListLogic";
import SidebarWeb from "@/components/menberFunction/functionNavbar/sidebarWeb.vue";
const { t } = useI18n();
import * as APItypes from "@/assets/API/APItype";

const { appContext } = getCurrentInstance() as ComponentInternalInstance;
const store = useCounterStore();
const memberService = useMemberService();
const timeListLogic = useTimeListLogic();
const {
  openBankVerified,
  openAdminBankList,
  AdminBankList,
  addMemberBankMsg,
  keepFormValues,
} = storeToRefs(memberService);
const { isWithdrawPageAlert } = storeToRefs(store);

//#region 實名認證
interface VerifyRealNameValue {
  sRealName: string;
  sBirthday: string;
  sIdCard: string;
}
const realNameForm = ref<null | InstanceType<typeof Form>>(null);

const eighteenYearsAgo = new Date();
eighteenYearsAgo.setFullYear(eighteenYearsAgo.getFullYear() - 18);
const BirthdayDefault = ref<string>(timeListLogic.DateString(eighteenYearsAgo));

async function submitVerifyRealName(FromValue: VerifyRealNameValue) {
  const { sRealName, sBirthday, sIdCard } = FromValue;
  const data = {
    sRealName,
    sBirthday,
    sIdCard,
  };
  await memberService.submitVerifyRealName(data);
}
//#endregion

//#region 新增信用卡
interface AddBankFormData {
  sPay_num: string;
  nPay_card: string;
  sPay_address: string;
  sBranchCode: string;
  nBankType: number;
}
const addBankForm = ref<null | InstanceType<typeof Form>>(null);
// 開戶銀行類別
const AdminBankId = ref<string>("");
const AdminBankKey = ref<string>("");
const getAdminBankKeyName = ref<string>("");

const getAdminBankList = computed(() => {
  const KeyList = AdminBankList.value.filter((item) => {
    return item.sBank.includes(AdminBankKey.value);
  });
  return KeyList;
});

const isAdminBankKey = ref<boolean>(false);
function openAdminBankKey() {
  isAdminBankKey.value = true;
}
function closeAdminBankKey() {
  isAdminBankKey.value = false;
}
function selectAdminBankKey(item: APItypes.AdminBankList) {
  closeAdminBankKey();
  console.log(item);
  getAdminBankKeyName.value = item.sBank;
  AdminBankKey.value = item.sBankId;
  AdminBankId.value = item.nId;
}
// 發送
async function openAddBankAlert(formValues: AddBankFormData) {
  const { sPay_num, sPay_address, sBranchCode } = formValues;

  const postData = {
    sPay_num,
    nPay_card: AdminBankId.value,
    sPay_address,
    sBranchCode,
    nBankType: 1,
    sFile0: null,
    sFile1: null,
  };
  keepFormValues.value = postData;
  store.toggleAlertBg();
  store.openAddBankAlert();
}
//#endregion

// 實名認證與新增信用卡同時submit
function submitAllForm() {
  const isRealNameForm = realNameForm.value?.validate().then((valid) => {
    return valid;
  });
  const isAddBankForm = addBankForm.value?.validate().then((valid) => {
    return valid;
  });
  if (!addBankForm.value) return;
  if (isRealNameForm && isAddBankForm) {
    // 會先進入新增信用卡的彈窗提示, 之後才會用彈窗的送出
    openAddBankAlert(addBankForm.value.getValues());
  }
}
async function submit() {
  if (!addBankForm.value) return;
  if (!realNameForm.value) return;
  memberService.sendAddMemberBank(keepFormValues.value);
  await submitVerifyRealName(realNameForm.value?.getValues());
  getAdminBankKeyName.value = "";
  AdminBankKey.value = "";
  AdminBankId.value = "";
  addBankForm.value.resetForm();
  realNameForm.value.resetForm();
}
onMounted(() => {
  appContext.config.globalProperties.$mitt.all.delete(
    "submit-add-bank",
    submit
  );
  appContext.config.globalProperties.$mitt.on("submit-add-bank", submit);
});
</script>
<template>
  <div
    class="md:grid grid-cols-12 gap-x-[3vw] block gap-y-[1dvh] px-[1vw] pb-[3rem] pt-0 md:pt-[3rem]">
    <SidebarWeb class="col-span-3 hidden md:block mb-[7dvh]" />
    <div class="col-span-9 relative">
      <div
        class="px-0 py-0 md:px-[5vw] md:py-[2dvh] relative z-10 web-gold-border rounded-[24px]">
        <p class="mt-[1dvh] text-[24px] text-primary font-bold hidden md:block">
          {{ $t("MemberFunction.BankVerified.Verified") }}
        </p>
        <div>
          <div
            v-if="!openBankVerified && !openAdminBankList"
            class="absolute top-1/2 left-1/2 submitting"></div>
          <div
            class="member-bg-border mt-[1vh] text-primary py-[1em] px-[8vw] md:px-[1vw] mx-[1em] rounded-[6px] pb-[80px]"
            v-else>
            <p
              class="text-[16px] md:text-[20px] ml-[2vw] font-bold relative left-[-2vw] before:absolute before:h-[60%] before:-translate-y-1/2 before:top-1/2 before:left-[-2vw] before:w-[3px] before:bg-yellow-100">
              {{ $t("MemberFunction.BankVerified.BasicInfo") }}
            </p>
            <VForm @submit="submitVerifyRealName" ref="realNameForm">
              <div>
                <label
                  for="sRealName"
                  class="grid grid-cols-8 text-[14px] md:text-[18px] mt-[3vh]">
                  <p class="mr-[1em] col-span-3">
                    {{ $t("MemberFunction.BankVerified.MemberName") }}
                  </p>
                  <div class="col-span-5">
                    <VField
                      class="bg-[url('@image/memberFunction/文字框/文字框_長.png')] bg-100 rounded-md px-[10px] w-full"
                      name="sRealName"
                      :label="$t('MemberFunction.BankVerified.MemberName')"
                      type="text"
                      :placeholder="
                        $t('MemberFunction.BankVerified.MemberNamePlaceholder')
                      "
                      :rules="{ required: true }" />
                    <ErrorMessage
                      name="sRealName"
                      class="text-red-600 md:ml-3 ml-0 text-[12px]" />
                  </div>
                </label>
                <label
                  for="sBirthday"
                  class="grid grid-cols-8 text-[14px] md:text-[18px] mt-[16px] md:mt-[2vh]">
                  <p class="mr-[1em] col-span-3">
                    {{ $t("MemberFunction.BankVerified.Birthday") }}
                  </p>
                  <div class="col-span-5">
                    <VField
                      class="bg-[url('@image/memberFunction/文字框/文字框_長.png')] bg-100 rounded-md pl-[10px] w-full search-date-label relative"
                      name="sBirthday"
                      :label="$t('MemberFunction.BankVerified.Birthday')"
                      type="date"
                      v-model="BirthdayDefault"
                      :rules="{ required: true }" />
                    <ErrorMessage
                      name="sBirthday"
                      class="text-red-600 md:ml-3 ml-0 text-[12px]" />
                  </div>
                </label>
                <label
                  for="sIdCard"
                  class="grid grid-cols-8 text-[14px] md:text-[18px] mt-[16px] md:mt-[2vh]">
                  <p class="mr-[1em] col-span-3">
                    {{ $t("MemberFunction.BankVerified.IdCardNumber") }}
                  </p>
                  <div class="col-span-5">
                    <VField
                      class="bg-[url('@image/memberFunction/文字框/文字框_長.png')] bg-100 rounded-md px-[10px] w-full"
                      name="sIdCard"
                      :label="$t('MemberFunction.BankVerified.IdCardNumber')"
                      type="text"
                      placeholder=""
                      :rules="{ required: true }" />
                    <ErrorMessage
                      name="sIdCard"
                      class="text-red-600 md:ml-3 ml-0 text-[12px]" />
                  </div>
                </label>
              </div>
            </VForm>
            <p
              class="text-[16px] md:text-[20px] ml-[2vw] mt-[2rem] font-bold relative left-[-2vw] before:absolute before:h-[60%] before:-translate-y-1/2 before:top-1/2 before:left-[-2vw] before:w-[3px] before:bg-yellow-100">
              {{ $t("MemberFunction.AddBank.AddBankCard") }}
            </p>
            <VForm @submit="openAddBankAlert" ref="addBankForm">
              <div class="text-primary grid grid-cols-1 grid-rows-1 gap-1">
                <label
                  for="sPay_num"
                  class="grid grid-cols-8 text-[14px] md:text-[18px] mt-[1dvh] md:mt-[2dvh]">
                  <p class="mr-[1em] col-span-3">
                    {{ $t("MemberFunction.AddBank.BankAccount") }}
                  </p>
                  <div class="col-span-5">
                    <VField
                      class="bg-[url('@image/memberFunction/文字框/文字框_長.png')] bg-100 rounded-md px-[10px] w-full"
                      name="sPay_num"
                      :label="$t('MemberFunction.AddBank.BankAccount')"
                      type="text"
                      :rules="{ required: true }" />
                    <ErrorMessage
                      name="sPay_num"
                      class="text-red-600 md:ml-3 ml-0 text-[12px]" />
                  </div>
                </label>
                <label
                  for="nPay_card"
                  class="grid grid-cols-8 text-[14px] md:text-[18px] mt-[1dvh] md:mt-[2dvh]">
                  <p class="mr-[1em] col-span-3">
                    {{ $t("MemberFunction.AddBank.OpeningBank") }}
                  </p>
                  <div class="col-span-5 relative">
                    <VField
                      class="bg-[url('@image/memberFunction/文字框/文字框_長.png')] bg-100 rounded-md px-[10px] w-full"
                      name="nPay_card"
                      :label="$t('MemberFunction.AddBank.OpeningBank')"
                      type="text"
                      :rules="{ required: true }"
                      :placeholder="$t('MemberFunction.AddBank.InputBankCode')"
                      @keyup="openAdminBankKey"
                      v-model="AdminBankKey" />
                    <ul
                      class="absolute z-[80] w-full max-h-[25dvh] overflow-y-auto bg-black rounded mt-[1dvh] md:mt-[2vh]"
                      v-if="isAdminBankKey">
                      <li
                        class="text-primary px-[2vw] py-[1dvh]"
                        v-if="getAdminBankList.length <= 0">
                        {{ $t("MemberFunction.AddBank.NoFindBankCode") }}
                      </li>
                      <li
                        v-for="item in getAdminBankList"
                        :key="item.sBankId + item.nId"
                        class="border-b border-white/50 last:border-b-0">
                        <button
                          type="button"
                          class="text-primary px-[2vw] py-[1dvh]"
                          @click.stop="selectAdminBankKey(item)">
                          {{ item.sBank }}
                        </button>
                      </li>
                    </ul>
                    <p class="text-primary/60 md:ml-3 ml-0 text-[12px]">
                      {{ getAdminBankKeyName }}
                    </p>
                    <ErrorMessage
                      name="nPay_card"
                      class="text-red-600 md:ml-3 ml-0 text-[12px]" />
                  </div>
                </label>

                <label
                  for="sPay_address"
                  class="grid grid-cols-8 text-[14px] md:text-[18px] mt-[1dvh] md:mt-[2dvh]">
                  <p class="mr-[1em] col-span-3">
                    {{ $t("MemberFunction.AddBank.OpeningBranch") }}
                  </p>
                  <div class="col-span-5">
                    <VField
                      class="col-span-1 bg-[url('@image/memberFunction/文字框/文字框_長.png')] bg-100 rounded-md px-[10px] w-full"
                      name="sPay_address"
                      :label="$t('MemberFunction.AddBank.OpeningBranch')"
                      type="text"
                      :rules="{ required: true }" />
                    <ErrorMessage
                      name="sPay_address"
                      class="text-red-600 md:ml-3 ml-0 text-[12px]" />
                  </div>
                </label>
                <label
                  for="sBranchCode"
                  class="grid grid-cols-8 text-[14px] md:text-[18px] mt-[1dvh] md:mt-[2dvh]">
                  <p class="mr-[1em] col-span-3">
                    {{ $t("MemberFunction.AddBank.BranchCode") }}
                  </p>
                  <div class="col-span-5">
                    <VField
                      class="col-span-1 bg-[url('@image/memberFunction/文字框/文字框_長.png')] bg-100 rounded-md px-[10px] w-full"
                      name="sBranchCode"
                      :label="$t('MemberFunction.AddBank.BranchCode')"
                      type="string"
                      :placeholder="
                        $t('MemberFunction.AddBank.InputBranchCode')
                      "
                      :rules="{ required: true, min: 4, max: 4 }" />
                    <a
                      href="https://www.fisc.com.tw/TC/Service?CAID=51254999-5d15-4ddf-8e54-4b2cdb2a8399"
                      target="blank"
                      class="underline text-[12px] text-blue-500 hover:text-blue-600 cursor-pointer"
                      >查詢代碼</a
                    >
                    <ErrorMessage
                      name="sBranchCode"
                      class="text-red-600 md:ml-3 ml-0 text-[12px]" />
                  </div>
                </label>
                <p class="text-red-600 mt-[12px]">
                  {{ addMemberBankMsg }}
                </p>
              </div>
              <!-- 注意事項 -->
              <div class="text-primary text-[14px] md:text-[16px] mt-[2dvh]">
                <div class="flex justify-center items-center leading-[36px]">
                  <img
                    src="@image/memberFunction/11/驚嘆號icon.png"
                    alt=""
                    class="w-[16px] mr-1" />
                  <span> {{ $t("MemberFunction.AddBank.Notice") }} </span>
                </div>
                <div class="leading-[20px] md:leading-[24px] pb-[1vh]">
                  <p>
                    <span class="text-red-600 mr-1">※</span
                    >{{ $t("MemberFunction.AddBank.NoticeList1") }}
                  </p>
                </div>
                <table class="leading-[20px] md:leading-[24px]">
                  <tbody>
                    <tr>
                      <td class="w-[30%] md:w-[20%] align-top pb-[1vh]">
                        <span class="text-red-600 mr-1">※</span
                        >{{ $t("MemberFunction.AddBank.NoticeList2") }}
                      </td>
                      <td class="pb-[1vh]">
                        {{ $t("MemberFunction.AddBank.NoticeList3") }}
                      </td>
                    </tr>
                    <tr>
                      <td class="align-top pb-[1vh]">
                        <span class="text-red-600 mr-1">※</span
                        >{{ $t("MemberFunction.AddBank.NoticeList4") }}
                      </td>
                      <td class="pb-[1vh]">
                        {{ $t("MemberFunction.AddBank.NoticeList5") }}
                      </td>
                    </tr>
                    <tr>
                      <td class="align-top pb-[1vh]">
                        <span class="text-red-600 mr-1">※</span
                        >{{ $t("MemberFunction.AddBank.NoticeList6") }}
                      </td>
                      <td class="pb-[1vh]">
                        {{ $t("MemberFunction.AddBank.NoticeList7") }}
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </VForm>
            <div class="col-span-3 flex justify-around mt-[12px] md:mt-[3dvh]">
              <button
                class="btn-y-border w-[100px] px-[em] py-[.1em]"
                type="button"
                @click="submitAllForm">
                <p class="text-[12px] md:text-[16px] text-secondary">
                  {{ $t("MemberFunction.BankVerified.Submit") }}
                </p>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
